/*
 *  Quantity Selector
 *    Define the quantity selector functionality
 *
 *  Version:
 *    1.0.0 - 2021/12/16
 */

import { CLASS_DISABLED } from "../../tools/constants/classes";

// Attributes
const ATTR_QUANTITY = 'data-quantity';
const ATTR_MIN = 'data-min';
const ATTR_MAX = 'data-max';

// Selectors
const SELECTOR_INPUT = '[data-quantity-selector-field]';
const SELECTOR_MINUS = '[data-quantity-selector-minus]';
const SELECTOR_PLUS = '[data-quantity-selector-plus]';

export class QuantitySelector {
  container: HTMLElement;
  quantity: number;
  min?: number;
  max?: number;
  minusDisabled: boolean;
  plusDisabled: boolean;
  minusButton: HTMLButtonElement|null;
  plusButton: HTMLButtonElement|null;

  constructor(container:HTMLElement) {
    this.container = container;

    // Initial values
    const qtyString = container.getAttribute(ATTR_QUANTITY);
    this.quantity = qtyString ? parseInt(qtyString) : 1;
    const minString = container.getAttribute(ATTR_MIN);
    this.min = minString ? parseInt(minString) : undefined;
    const maxString = container.getAttribute(ATTR_MAX);
    this.max = maxString ? parseInt(maxString) : undefined;

    //@ts-ignore
    this.minusDisabled = typeof this.min === typeof undefined ? false : this.quantity <= this.min;
    //@ts-ignore
    this.plusDisabled = typeof this.max === typeof undefined ? false : this.quantity >= this.max;

    // Elements
    this.minusButton = container.querySelector<HTMLButtonElement>(SELECTOR_MINUS);
    this.plusButton = container.querySelector<HTMLButtonElement>(SELECTOR_PLUS);

    // Events
    if(this.minusButton) this.minusButton.addEventListener('click', (e) => this.onMinusClick());
    if(this.plusButton) this.plusButton.addEventListener('click', (e) => this.onPlusClick());
  }

  setQuantity(qty:number) {
    //@ts-ignore
    if(typeof this.min !== typeof undefined) qty = Math.max(this.min, qty);
    //@ts-ignore
    if(typeof this.max !== typeof undefined) qty = Math.min(this.max, qty);

    this.quantity = qty;
    this.container.setAttribute(ATTR_QUANTITY, qty.toString());

    const input = this.container.querySelector<HTMLInputElement>(SELECTOR_INPUT);
    if(input) input.value = qty.toString();
  }

  updateDisabled() {
    //@ts-ignore
    this.minusDisabled = typeof this.min === typeof undefined ? false : this.quantity <= this.min;
    //@ts-ignore
    this.plusDisabled = typeof this.max === typeof undefined ? false : this.quantity >= this.max;

    // Update minus button disabled status
    if(this.minusButton) {
      if(this.minusDisabled) {
        this.minusButton.setAttribute('disabled', 'disabled');
        this.minusButton.classList.add(CLASS_DISABLED);
      } else {
        this.minusButton.removeAttribute('disabled');
        this.minusButton.classList.remove(CLASS_DISABLED);
      }
    }

    // Update plus button disabled status
    if(this.plusButton) {
      if(this.plusDisabled) {
        this.plusButton.setAttribute('disabled', 'disabled');
        this.plusButton.classList.add(CLASS_DISABLED);
      } else {
        this.plusButton.removeAttribute('disabled');
        this.plusButton.classList.remove(CLASS_DISABLED);
      }
    }
  }

  onPlusClick() {
    if(this.plusDisabled) return;
    this.setQuantity(this.quantity + 1);
    this.updateDisabled();
  }

  onMinusClick() {
    if(this.minusDisabled) return;
    this.setQuantity(this.quantity - 1);
    this.updateDisabled();
  }

  setMax(max:number) {
    this.max = max;
  }
}
