//@ts-ignore
import * as Animation from '../../styles/settings/animation.scss';
const cssTime = (s:string) => {
  if(!s) return -1;
  if(s.indexOf('ms') !== -1) {
    return parseInt(s);
  } else if(s.indexOf('s') !== -1) {
    return parseFloat(s) * 1000;
  }
  return -1;
}

export const short = cssTime(Animation.default.short);
export const medium = cssTime(Animation.default.med);
export const slow = cssTime(Animation.default.slow);

export const easeIn = Animation.default.easeIn as string;
export const easeOut = Animation.default.easeOut as string;
export const easeInOut = Animation.default.easeInOut as string;