import { initAccordion } from '../../objects/accordion/accordion';
import { responsiveMediaAbove } from '../../settings/responsive';
import { CLASS_ACTIVE, CLASS_INITIALISED } from '../../tools/constants/classes';

// Selectors
const SELECTOR_ACCORDION = '[data-accordion-container]';
const SELECTOR_CONTAINER = '[data-product-details]';
const SELECTOR_SLIDE = '[data-accordion-slide]';
const SELECTOR_TITLE = '[data-accordion-title]';
const SELECTOR_ACTIVE_BLOB = '[data-active-tab-blob]';

const ATTR_TABS_COUNT = 'data-tabs-count';

document.querySelectorAll<HTMLElement>(SELECTOR_CONTAINER).forEach(containerElement => {
  // Check if section has already been initialized
  if (containerElement.classList.contains(CLASS_INITIALISED)) return;
  containerElement.classList.add(CLASS_INITIALISED);

  const accordion = containerElement.querySelector<HTMLDetailsElement>(SELECTOR_ACCORDION);
  const slides = containerElement.querySelectorAll<HTMLDetailsElement>(SELECTOR_SLIDE);
  const activeBlob = containerElement.querySelector<HTMLElement>(SELECTOR_ACTIVE_BLOB);
  const accordionTitles = Array.from(containerElement.querySelectorAll<HTMLElement>(SELECTOR_TITLE));


  if (!accordion || !slides || !accordionTitles) return;

  // Initialize accordion
  initAccordion({ container: accordion, disableSize: "MEDIUM" });

  // Add event listener for each tab
  slides.forEach((slide) => {
    slide.addEventListener('click', (e) => tabClick(e, slide));
  });

  /**
   *
   * @param e Click event
   * @param slide Accordion element
   * Removes class active on all tabs and adds to the clicked tab
   * On Desktop there will be a blob behind the active tab that moves
   * into place when a tab is clicked
   */
  const tabClick = (e: Event, slide: HTMLDetailsElement) => {

    const target = e.target as HTMLElement; // Element clicked
    const targetTab = target.closest(SELECTOR_TITLE) as HTMLElement; // Closest tab element of target
    const idx: number = accordionTitles.indexOf(targetTab); // Index of clicked tab in array of tabs
    const numberOfTabs = accordion.getAttribute(ATTR_TABS_COUNT); // number of tabs

    if (!targetTab || !activeBlob || idx === null) return;

    // Remove active class on all slides
    slides.forEach(slide => {
      slide.classList.remove(CLASS_ACTIVE);
    });

    // Add active class to target tab
    slide.classList.add(CLASS_ACTIVE);

    // Move tab blob over to the index
    switch(numberOfTabs){
      case '1':
        break;
      case'2':
        activeBlob.style.left = `${idx * 50 + 11}%`;
        break;
      case '3':
        activeBlob.style.left = `${idx * 33 + 3}%`;
        break;
      case '4':
        activeBlob.style.left = `${idx * 25}%`;
        break;
    }
  }


  /**
   * Close accordions on mobile sizes to work like regular accordion
   * and open on desktop to give appearance of tabs
   */
  const openCloseSlides = () => {
    if (responsiveMediaAbove('MEDIUM')) {
      let activeSlide = false;
      slides.forEach((slide) => {
        slide.setAttribute('open', 'open');
        if (slide.classList.contains(CLASS_ACTIVE)) activeSlide = true;
      });

      if (!activeSlide) slides[0].classList.add(CLASS_ACTIVE);
    } else {
      slides.forEach((slide) => {
        if (!slide.classList.contains(CLASS_ACTIVE)) {
          slide.removeAttribute('open');
        }
      });
    }
  }

  window.addEventListener('load', openCloseSlides);
  window.addEventListener('resize', openCloseSlides);
});
